.gpt3__cta {
    border: 1px solid #fff;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);

    &-content {
        p {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 12px;
            line-height: 30px;
            color: #0e0e0e;
        }

        h3 {
            font-family: var(--font-family);
            font-weight: 800;
            font-size: 24px;
            line-height: 45px;
            color: #000;
        }
    }

    &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 2rem;

        button {
            background: #000;
            color: #fff;
            border-radius: 40px;
            font-family: var(--font-family);
            font-weight: boldl;
            font-size: 18px;
            line-height: 30px;
            padding: .5rem 1rem;
            border: none;
            outline: none;
            cursor: pointer;
            min-width: 150px;
        }
    }
}

@media screen and (max-width: 650px) {
    .gpt3__cta {
        flex-direction: column;

        &-btn {
            margin: 2rem 0 0;
        }
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;

        &-content {
            h3 {
                font-size: 18px;
                line-height: 32px;
            }
        }

        &-btn {
            button {
                font-size: 14px;
                line-height: 28px;
            }
        }
    }
}